@import url("./Nullify.scss");
@import url("https://fonts.googleapis.com/css2?family=League+Spartan:wght@700&display=swap");

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--display-numbers);
  font-size: 32px;
  font-weight: 700;
  font-family: League Spartan;
  background-color: var(--app-bg);
  display: flex;
  align-items: center;
  justify-content: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --desaturated-dark-blue: hsl(225, 21%, 49%);
  --Desaturated-dark-blue-shadow: hsl(224, 28%, 35%);

  --red: hsl(6, 63%, 50%);
  --dark-red: hsl(6, 70%, 34%);

  --light-grayish-orange: hsl(30, 25%, 89%);
  --grayish-orange: hsl(28, 16%, 65%);

  --very-dark-grayish-blue: hsl(221, 14%, 31%);
  --white: hsl(0, 0%, 100%);

  --dark-moderate-cyan: hsl(185, 42%, 37%);
  --very-dark-cyan: hsl(185, 58%, 25%);

  --orange: hsl(25, 98%, 40%);
  --dark-orange: hsl(25, 99%, 27%);

  --light-grayish-yellow: hsl(45, 7%, 89%);
  --dark-grayish-orange: hsl(35, 11%, 61%);

  --very-dark-grayish-yellow: hsl(60, 10%, 19%);

  --dark-violet: hsl(281, 89%, 26%);
  --vivid-magenta: hsl(285, 91%, 52%);

  --pure-cyan: hsl(176, 100%, 44%);
  --soft-cyan: hsl(177, 92%, 70%);

  --very-dark-violet: hsl(268, 47%, 21%);
  --dark-magenta: hsl(290, 70%, 36%);

  --light-yellow: hsl(52, 100%, 62%);
  --very-dark-blue: hsl(198, 20%, 13%);
}
.App {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;

  flex-direction: column;
  padding-top: 1rem;
}
body[data-attribute="theme-one"] {
  --display-numbers: var(--white);
  --app-bg: var(--dark-moderate-cyan);

  --main-background: hsl(222, 26%, 31%);
  --toggle-background: hsl(223, 31%, 20%);
  --screen--background: hsl(224, 36%, 15%);

  --key-del-reset: hsl(225, 21%, 49%);
  --key-del-reset-shadow: hsl(224, 28%, 35%);

  --key-total: hsl(6, 63%, 50%);
  --key-total-shadow: hsl(6, 70%, 34%);

  --key-numbers: hsl(30, 25%, 89%);
  --key-numbers-shadow: hsl(28, 16%, 65%);

  --text-numbers-color: hsl(221, 14%, 31%);
  --key-text-color-del-reset: hsl(0, 0%, 100%);
  --del-reset-text: var(--white);
  --input-color: var(--red);
  --display-bg: hsl(224, 36%, 15%);
}

body[data-attribute="theme-two"] {
  --display-numbers: var(--very-dark-grayish-yellow);
  --app-bg: var(--soft-cyan);
  --main-background: hsl(0, 0%, 90%);
  --toggle-background: hsl(0, 5%, 81%);
  --screen--background: hsl(0, 5%, 81%);

  --key-del-reset: var(--dark-moderate-cyan);
  --key-del-reset-shadow: var(--very-dark-cyan);

  --key-total: var(--orange);
  --key-total-shadow: var(--dark-orange);

  --key-numbers: var(--light-grayish-yellow);
  --key-numbers-shadow: var(--dark-grayish-orange);

  --text-numbers-color: var(--very-dark-grayish-yellow);
  --key-text-color-del-reset: var(--white);
  --del-reset-text: var(--white);
  --input-color: var(--orange);
  --display-bg: var(--white);
}

body[data-attribute="theme-three"] {
  --display-numbers: var(--light-yellow);
  --app-bg: var(--dark-red);
  --main-background: hsl(268, 75%, 9%);
  --toggle-background: hsl(268, 71%, 12%);
  --screen--background: hsl(268, 71%, 12%);

  --key-del-reset: var(--dark-violet);
  --key-del-reset-shadow: var(--dark-magenta);
  --del-reset-text: var(--white);

  --key-total: var(--pure-cyan);
  --key-total-shadow: var(--soft-cyan);

  --key-numbers: var(--very-dark-violet);
  --key-numbers-shadow: var(--dark-magenta);

  --text-numbers-color: var(--light-yellow);
  --key-text-color-del-reset: var(--white);

  --input-color: var(--pure-cyan);
  --display-bg: var(--very-dark-violet);
}

///////////////////
///
///
input {
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  max-width: 60px;
  cursor: pointer;
  outline: none;
  overflow: hidden;
  border-radius: 16px;
}
.theme-input-ranges {
  width: 60px;
  display: flex;
  justify-content: space-between;
  label {
    font-size: 16px;
  }
}

input::-webkit-slider-runnable-track {
  height: 20px;
  background: var(--toggle-background);
  border-radius: 16px;
}

input::-moz-range-track {
  height: 20px;
  background: var(--toggle-background);
  border-radius: 16px;
}

input::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  height: 20px;
  width: 20px;
  background-color: var(--input-color);
  border-radius: 50%;
  border: 2px solid var(--input-color);
  box-shadow: -407px 0 0 400px var(--toggle-background);
}

input::-moz-range-thumb {
  height: 20px;
  width: 20px;
  background-color: var(--input-color);
  border-radius: 50%;
  border: 1px solid var(--input-color);
  box-shadow: -407px 0 0 400px var(--toggle-background);
}

.calc-content-grid {
  background-color: var(--main-background);
  max-width: 520px;
  width: 100%;
  height: 100%;
  padding: 1rem;
  @media (max-width: 520px) {
    max-width: 375px;
  }
}

.buttons-grid {
  display: grid;
  width: 100%;
  height: 100%;
  margin-top: 3rem;
  grid-template-columns: repeat(4, 6rem);
  grid-template-rows: minmax(6rem, auto) repeat(4, 6rem);
  gap: 1rem;
  padding: 2rem;
  background-color: var(--screen--background);
  border-radius: 15px;
  max-height: 100%;
  @media (max-width: 520px) {
    grid-template-columns: repeat(4, 4rem);
    grid-template-rows: minmax(4rem, auto) repeat(4, 4rem);
  }
}
.span-two {
  grid-column: span 2;
}
button {
  display: inline-block;
  border-radius: 15px;

  color: var(--text-numbers-color);
  background-color: var(--key-numbers);
  box-shadow: 1px 7px 3px var(--key-numbers-shadow);
  &.delete,
  &.reset,
  &.total {
    color: var(--del-reset-text);
    box-shadow: 1px 7px 3px var(--key-del-reset-shadow);
    background-color: var(--key-del-reset);
  }
}
.calc-header {
  display: flex;
  justify-content: space-between;
}
.cal-theme-switcher {
  display: flex;
  align-items: center;
  gap: 25px;
  p {
    font-size: 16px;
  }
}
.calc-output {
  background-color: var(--display-bg);
  width: 100%;
  height: 6rem;
  padding: 1rem;
  border-radius: 15px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
